@keyframes float {
  0%, 100% {
    transform: translateY(0px) scale(1);
  }
  50% {
    transform: translateY(-20px) scale(1.02);
  }
}
@keyframes bounce {
  0%, 100% {
    transform: translateY(0px) scale(1);
    animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }
  50% {
    transform: translateY(-10px) scale(1.05);
    animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }
}
@keyframes glow {
  0%, 100% {
    box-shadow: 0 0 20px rgba(59, 130, 246, 0.3);
    transform: scale(1);
  }
  50% {
    box-shadow: 0 0 40px rgba(59, 130, 246, 0.6);
    transform: scale(1.02);
  }
}
@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}
@keyframes liquid-flow {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
    opacity: 0.6;
  }
  25% {
    transform: translateY(-5px) rotate(1deg);
    opacity: 0.8;
  }
  50% {
    transform: translateY(-10px) rotate(0deg);
    opacity: 1;
  }
  75% {
    transform: translateY(-5px) rotate(-1deg);
    opacity: 0.8;
  }
}
@keyframes spring-in {
  0% {
    transform: scale(0.8) translateY(20px);
    opacity: 0;
  }
  60% {
    transform: scale(1.05) translateY(-5px);
    opacity: 0.8;
  }
  100% {
    transform: scale(1) translateY(0px);
    opacity: 1;
  }
}
.float-animation {
  animation: float 6s cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite;
}
.bounce-animation {
  animation: bounce 2s cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite;
}
.glow-animation {
  animation: glow 3s cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite;
}
.liquid-flow {
  animation: liquid-flow 4s cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite;
}
.spring-in {
  animation: spring-in 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) forwards;
}
.shimmer {
  position: relative;
  overflow: hidden;
}
.shimmer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  animation: shimmer 2s infinite;
}
